import {IconCategory} from '@here/hds-iconlibrary';
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {
  ListItem,
  ListItemVariant,
  ListVariant,
  ListItemSize,
  ListPadding,
  ListItemSelectPayload,
} from '@here/hds-list';

// eslint-disable-next-line no-new
new ListItem();
export interface HDSListItemProps
  extends Omit<React.HTMLAttributes<HTMLDivElement>, 'onSelect' | 'unselectable'> {
  selected?: boolean;
  unhoverable?: boolean;
  unselectable?: boolean;
  unpressable?: boolean;
  unfocusable?: boolean;
  disabled?: boolean;
  dragging?: boolean;
  hasTooltip?: boolean;
  hasAnchor?: boolean;
  icon?: string;
  iconCategory?: IconCategory;
  iconRight?: string;
  iconRightCategory?: IconCategory;
  variant?: ListItemVariant;
  groupVariant?: ListVariant;
  size?: ListItemSize;
  horizontalPadding?: ListPadding;
  // eslint-disable-next-line no-unused-vars
  onSelect?: (e: ListItemSelectPayload) => void;
}

const HDSListItem = React.forwardRef(
  (
    {
      unhoverable,
      unselectable,
      unpressable,
      unfocusable,
      selected,
      disabled,
      dragging,
      children,
      iconRight,
      iconRightCategory,
      iconCategory,
      className,
      onSelect,
      ...rest
    }: HDSListItemProps,
    ref
  ) => {
    const innerRef = useRef<HTMLElement>(null);
    useImperativeHandle(ref, () => innerRef.current);

    const _handleSelected = (e: CustomEvent<ListItemSelectPayload>) => {
      if (e.target !== innerRef.current) return;
      onSelect && onSelect(e.detail);
    };

    useEffect(() => {
      if (innerRef.current) {
        if (onSelect) {
          innerRef.current.addEventListener('select', _handleSelected as EventListener);
        }
      }
      return () => {
        innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
      };
    }, [onSelect, innerRef]);

    return (
      <hds-list-item
        {...(unhoverable && {unhoverable})}
        {...(unselectable && {unselectable})}
        {...(unpressable && {unpressable})}
        {...(unfocusable && {unfocusable})}
        {...(selected && {selected})}
        {...(disabled && {disabled})}
        {...(dragging && {dragging})}
        {...(iconRight && {'icon-right': iconRight})}
        {...(iconRightCategory && {'icon-right-category': iconRightCategory})}
        {...(iconCategory && {'icon-category': iconCategory})}
        {...(className && {class: className})}
        ref={innerRef}
        {...rest}
      >
        {children}
      </hds-list-item>
    );
  }
);

HDSListItem.defaultProps = {
  unhoverable: false,
  unpressable: false,
  unselectable: false,
  unfocusable: false,
  selected: false,
  dragging: false,
  icon: undefined,
  iconCategory: 'core-ui',
  iconRight: undefined,
  iconRightCategory: 'core-ui',
  variant: 'default',
  groupVariant: 'subtle',
  size: 'small',
};

export default HDSListItem;
